import React from 'react';
import "./Country.css"
const Country = (props) => {
	// Destructuring
	const {flag, name, capital, population, region} = props.allCountry;
	return (
		<div>
			<img src={flag} alt="country" />
			<br />
			<br />
			<h4 className="mb-7"><span className="color-text">Name:</span> {name}</h4>
			<p className="mb-7"><span className="color-text">Capital:</span> {capital}</p>
			<p className="mb-7"><span className="color-text">Population:</span> {population}</p>
			<p><span className="color-text">Region:</span> {region}</p>
		</div>
	);
};

export default Country;